.tooltip{
  margin-left: 10px;

  &__btn{
    @include flex-all-center;

    width: 24px;
    height: 24px;

    border-radius: 100%;
    background-color: rgba(20, 16, 36, 0.08);

    svg {
      width: 4px;
      height: 14px;
    }

    &:focus {
        + .tooltip__txt{
          opacity: 1;
          visibility: visible;
      }
    }

    @include hover{
      + .tooltip__txt{
        opacity: 1;
        visibility: visible;
      }
    }
  }


  &__txt{
    padding: 15px 20px;

    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    text-align: center;
    color: var(--dark-color);

    border-radius: 5px;
    box-shadow: 0 2px 8px 0 rgba(20, 16, 36, 0.1);
    background-color: var(--light-color);

    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }
}

