.trainers{
  &__wrapper{
    padding-left: max(15px, calc(100% - var(--content-width)) / 2);
  }

  &__image{
    margin: 0;
    margin-bottom: 20px;
    max-height: 500px;

    border-radius: 20px;
  }
  &__name {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 600;
    font-size: 24px;
    line-height: 160%;
    color: var(--dark-color);
  }
  &__post {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    color: var(--text-color);
  }
  &__btn {
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    color: var(--accent-color);
  }
  &__nav{
   @include flex-all-sb;
   position: relative;
   width: 360px;
  }
  &__scroll{
    // width: 360px;
    height: 5px;
    border-radius: 40px;
    background-color: rgba(20, 16, 36, 0.08);
  }
  &__content{
    margin-bottom: 75px;
  }

  &__slider-btn{
    @include bg-cover;
    width: 40px;
    height: 40px;

    &:not(:last-child){
      margin-right: 10px;
    }
  }

}
.swiper-scrollbar-drag{
  background-color: var(--dark-color);
}
.trsiners__slider-btn--prev{
  background-image: url('../../img/prev-btn.svg');
}
.trsiners__slider-btn--next{
  background-image: url('../../img/next-btn.svg');
}
.container__nav{
  @include flex-all-sb;
}